import { lang } from './Lang';
import { Config } from './Config';
import { msgBox } from './MsgBox';
import { EVT } from './EVT';
import { setSetting, settings } from './setting/Settings';
// 显示最近更新内容
class ShowWhatIsNew {
    constructor() {
        this.flag = '3.0.0';
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.settingInitialized, () => {
            // 消息文本要写在 settingInitialized 事件回调里，否则它们可能会被翻译成错误的语言
            let msg = `${lang.transl('_whatisnew')}
      <br>
      · ${lang.transl('_在序号前面填充0')}
      <br>
      · ${lang.transl('_下载完成后显示通知')}
      <br>
      · ${lang.transl('_不下载重复文件')}
      <br>
      · ${lang.transl('_背景图片')}
      `;
            // 在更新说明的下方显示赞助提示
            msg += `
      <br>
      <br>
      ${lang.transl('_赞助方式提示')}`;
            this.show(msg);
        });
    }
    show(msg) {
        if (settings.whatIsNewFlag !== this.flag) {
            msgBox.show(msg, {
                title: Config.appName + ` ${lang.transl('_最近更新')}`,
                btn: lang.transl('_我知道了'),
            });
            setSetting('whatIsNewFlag', this.flag);
        }
    }
}
new ShowWhatIsNew();
