import { lang } from './Lang';
import { Colors } from './Colors';
import { Tools } from './Tools';
import { InitPageBase } from './InitPageBase';
import { API } from './API';
class InitHomePage extends InitPageBase {
    constructor() {
        super();
        this.init();
    }
    // 添加中间按钮
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, lang.transl('_抓取赞助的所有用户的投稿')).addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    async FetchPostList() {
        let data;
        if (this.nextUrl) {
            data = (await API.request(this.nextUrl));
        }
        else {
            data = await API.getPostListSupporting(300);
        }
        this.afterFetchPostList(data);
    }
}
export { InitHomePage };
