// 下载控制
import { EVT } from '../EVT';
import { Tools } from '../Tools';
import { store } from '../Store';
import { log } from '../Log';
import { lang } from '../Lang';
import { titleBar } from '../TitleBar';
import { Colors } from '../Colors';
import { Download } from './Download';
import { progressBar } from '../ProgressBar';
import { settings } from '../setting/Settings';
import { states } from '../States';
import { ShowSkipCount } from './ShowSkipCount';
import { msgBox } from '../MsgBox';
class DownloadControl {
    constructor() {
        this.downloadThreadMax = 6; // 同时下载的线程数的最大值，也是默认值
        this.downloadThread = 3; // 同时下载的线程数
        this.taskBatch = 0; // 标记任务批次，每次重新下载时改变它的值，传递给后台使其知道这是一次新的下载
        this.statesList = []; // 下载状态列表，保存每个下载任务的状态
        this.taskList = {}; // 下载任务列表，使用下载的文件的 id 做 key，保存下载栏编号和它在下载状态列表中的索引
        this.downloaded = 0; // 已下载的任务数量
        this.reTryTimer = 0; // 重试下载的定时器
        this.wrapper = document.createElement('div');
        this.totalNumberEl = document.createElement('span');
        this.downStatusEl = document.createElement('span');
        this.downloadStop = false; // 是否停止下载
        this.downloadPause = false; // 是否暂停下载
        this.msgFlag = 'uuidTip';
        this.createDownloadArea();
        this.bindEvents();
        const skipTipWrap = this.wrapper.querySelector('.skip_tip');
        new ShowSkipCount(skipTipWrap);
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.hideDownloadArea();
            this.reset();
        });
        window.addEventListener(EVT.list.crawlFinish, () => {
            if (store.result.length === 0) {
                return progressBar.reset(0);
            }
            this.showDownloadArea();
            this.beforeDownload();
        });
        window.addEventListener(EVT.list.skipDownload, (ev) => {
            // 跳过下载的文件不会触发 downloadSuccess 事件
            const data = ev.detail.data;
            this.downloadSuccess(data);
        });
        // 监听浏览器下载文件后，返回的消息
        chrome.runtime.onMessage.addListener((msg) => {
            var _a;
            if (!this.taskBatch) {
                return;
            }
            // UUID 的情况
            if ((_a = msg.data) === null || _a === void 0 ? void 0 : _a.uuid) {
                log.error(lang.transl('_uuid'));
                msgBox.once(this.msgFlag, lang.transl('_uuid'), 'error');
            }
            // 文件下载成功
            if (msg.msg === 'downloaded') {
                EVT.fire('downloadSuccess', msg.data);
                this.downloadSuccess(msg.data);
            }
            else if (msg.msg === 'download_err') {
                // 浏览器把文件保存到本地时出错
                if (msg.err === 'SERVER_BAD_CONTENT') {
                    log.error(`${msg.data.url} Download error! Code: ${msg.err}. 404: file does not exist.`);
                    // 404 错误不重试下载
                }
                else {
                    log.error(`${msg.data.url} Download error! Code: ${msg.err}. Will try again later.`);
                    // 重新下载这个文件
                    this.downloadError(msg.data, msg.err);
                }
                EVT.fire('downloadError');
            }
            // UUID 的情况
            if (msg.data && msg.data.uuid) {
                log.error(lang.transl('_uuid'));
            }
        });
    }
    set setDownloaded(val) {
        this.downloaded = val;
        this.LogDownloadStates();
        // 设置下载进度信息
        this.totalNumberEl.textContent = store.result.length.toString();
        progressBar.setTotalProgress(this.downloaded);
        // 重置下载进度信息
        if (this.downloaded === 0) {
            this.setDownStateText(lang.transl('_未开始下载'));
        }
        // 下载完毕
        if (this.downloaded === store.result.length) {
            EVT.fire('downloadComplete');
            this.reset();
            this.setDownStateText(lang.transl('_下载完毕'));
            log.success(lang.transl('_下载完毕'), 2);
            titleBar.change('√');
        }
    }
    downloadedAdd() {
        this.setDownloaded = this.downloaded + 1;
    }
    // 显示或隐藏下载区域
    showDownloadArea() {
        this.wrapper.style.display = 'block';
    }
    hideDownloadArea() {
        this.wrapper.style.display = 'none';
    }
    // 设置下载状态文本，默认颜色为主题蓝色
    setDownStateText(str, color = '') {
        const el = document.createElement('span');
        el.textContent = str;
        if (color) {
            el.style.color = color;
        }
        this.downStatusEl.innerHTML = '';
        this.downStatusEl.appendChild(el);
    }
    reset() {
        this.statesList = [];
        this.downloadPause = false;
        this.downloadStop = false;
        clearTimeout(this.reTryTimer);
    }
    createDownloadArea() {
        const html = `<div class="download_area">
    <div class="centerWrap_btns">
    <button class="startDownload" type="button" style="background:${Colors.bgBlue};" data-xztext="_开始下载"></button>
    <button class="pauseDownload" type="button" style="background:${Colors.bgYellow};" data-xztext="_暂停下载"></button>
    <button class="stopDownload" type="button" style="background:${Colors.bgRed};" data-xztext="_停止下载"></button>
    <button class="previewFileName" type="button" style="background:${Colors.bgGreen};" data-xztext="_预览文件名"></button>
    </div>
    <div class="download_status_text_wrap">
    <span data-xztext="_当前状态"></span>
    <span class="down_status" data-xztext="_未开始下载"></span>
    <span class="skip_tip warn"></span>
    </div>
    </div>`;
        this.wrapper = Tools.useSlot('downloadArea', html);
        lang.register(this.wrapper);
        this.downStatusEl = this.wrapper.querySelector('.down_status');
        this.wrapper
            .querySelector('.startDownload')
            .addEventListener('click', () => {
            this.startDownload();
        });
        this.wrapper
            .querySelector('.pauseDownload')
            .addEventListener('click', () => {
            this.pauseDownload();
        });
        this.wrapper
            .querySelector('.stopDownload')
            .addEventListener('click', () => {
            this.stopDownload();
        });
        this.wrapper
            .querySelector('.previewFileName')
            .addEventListener('click', () => {
            EVT.fire('previewFileName');
        });
    }
    // 下载线程设置
    setDownloadThread() {
        const setThread = settings.downloadThread;
        if (setThread < 1 ||
            setThread > this.downloadThreadMax ||
            isNaN(setThread)) {
            // 如果数值非法，则重设为默认值
            this.downloadThread = this.downloadThreadMax;
        }
        else {
            this.downloadThread = setThread; // 设置为用户输入的值
        }
        // 如果剩余任务数量少于下载线程数
        if (store.result.length - this.downloaded < this.downloadThread) {
            this.downloadThread = store.result.length - this.downloaded;
        }
        // 重设下载进度条
        progressBar.reset(this.downloadThread, this.downloaded);
    }
    // 抓取完毕之后，已经可以开始下载时，根据一些状态进行处理
    beforeDownload() {
        this.setDownloaded = 0;
        this.setDownloadThread();
        if (!settings.autoStartDownload && !states.quickCrawl) {
            titleBar.change('▶');
        }
        // 视情况自动开始下载
        if (settings.autoStartDownload || states.quickCrawl) {
            this.startDownload();
        }
    }
    // 开始下载
    startDownload() {
        // 如果正在下载中，或无图片，则不予处理
        if (states.busy || store.result.length === 0) {
            return;
        }
        // 如果之前不是暂停状态，则需要重新下载
        if (!this.downloadPause) {
            this.setDownloaded = 0;
            // 初始化下载记录
            // 状态：
            // -1 未使用
            // 0 使用中
            // 1 已完成
            this.statesList = new Array(store.result.length).fill(-1);
            this.taskBatch = new Date().getTime(); // 修改本批下载任务的标记
        }
        else {
            // 继续下载
            // 把“使用中”的下载状态重置为“未使用”
            for (let index = 0; index < this.statesList.length; index++) {
                if (this.statesList[index] === 0) {
                    this.statesList[index] = -1;
                }
            }
        }
        // 重置一些条件
        this.downloadPause = false;
        this.downloadStop = false;
        clearTimeout(this.reTryTimer);
        this.setDownloadThread();
        EVT.fire('downloadStart');
        // 启动或继续下载，建立并发下载线程
        for (let i = 0; i < this.downloadThread; i++) {
            this.createDownload(i);
        }
        this.setDownStateText(lang.transl('_正在下载中'));
        log.log(lang.transl('_正在下载中'));
    }
    // 暂停下载
    pauseDownload() {
        clearTimeout(this.reTryTimer);
        if (store.result.length === 0) {
            return;
        }
        // 停止的优先级高于暂停。点击停止可以取消暂停状态，但点击暂停不能取消停止状态
        if (this.downloadStop === true) {
            return;
        }
        if (this.downloadPause === false) {
            // 如果正在下载中
            if (states.busy) {
                this.downloadPause = true; // 发出暂停信号
                EVT.fire('downloadPause');
                titleBar.change('║');
                this.setDownStateText(lang.transl('_已暂停'), '#f00');
                log.warning(lang.transl('_已暂停'), 2);
            }
            else {
                // 不在下载中的话不允许启用暂停功能
                return;
            }
        }
    }
    // 停止下载
    stopDownload() {
        clearTimeout(this.reTryTimer);
        if (store.result.length === 0 || this.downloadStop) {
            return;
        }
        this.downloadStop = true;
        EVT.fire('downloadStop');
        titleBar.change('■');
        this.setDownStateText(lang.transl('_已停止'), '#f00');
        log.error(lang.transl('_已停止'), 2);
        this.downloadPause = false;
    }
    downloadError(data, err) {
        if (this.downloadPause || this.downloadStop) {
            return false;
        }
        const task = this.taskList[data.id];
        // 复位这个任务的状态
        this.setDownloadedIndex(task.index, -1);
        // 建立下载任务，再次下载它
        // 如果出现了服务端错误，可能是获取原图时出现错误，改为使用缩略图进行下载
        this.createDownload(task.progressBarIndex, err === 'SERVER_FAILED');
    }
    downloadSuccess(data) {
        const task = this.taskList[data.id];
        // 更改这个任务状态为“已完成”
        this.setDownloadedIndex(task.index, 1);
        // 增加已下载数量
        this.downloadedAdd();
        // 是否继续下载
        const no = task.progressBarIndex;
        if (this.checkContinueDownload()) {
            this.createDownload(no);
        }
    }
    // 设置已下载列表中的标记
    setDownloadedIndex(index, value) {
        this.statesList[index] = value;
    }
    // 当一个文件下载完成后，检查是否还有后续下载任务
    checkContinueDownload() {
        // 如果没有全部下载完毕
        if (this.downloaded < store.result.length) {
            // 如果任务已停止
            if (this.downloadPause || this.downloadStop) {
                return false;
            }
            // 如果已完成的数量 加上 线程中未完成的数量，仍然没有达到文件总数，继续添加任务
            if (this.downloaded + this.downloadThread - 1 < store.result.length) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }
    // 在日志上显示下载进度
    LogDownloadStates() {
        let text = `${this.downloaded} / ${store.result.length}`;
        log.log(text, 2, false);
    }
    // 查找需要进行下载的作品，建立下载
    // 可选第二个参数：使用缩略图 url 而不是原图 url 进行下载
    createDownload(progressBarIndex, useThumb = false) {
        let length = this.statesList.length;
        let index;
        for (let i = 0; i < length; i++) {
            if (this.statesList[i] === -1) {
                this.statesList[i] = 0;
                index = i;
                break;
            }
        }
        if (index === undefined) {
            throw new Error('There are no data to download');
        }
        else {
            let result = store.result[index];
            if (useThumb && result.retryUrl) {
                ;
                [result.url, result.retryUrl] = [result.retryUrl, result.url];
            }
            const data = {
                id: result.fileId,
                data: result,
                index: index,
                progressBarIndex: progressBarIndex,
                taskBatch: this.taskBatch,
            };
            // 保存任务信息
            this.taskList[data.data.fileId] = {
                index,
                progressBarIndex: progressBarIndex,
            };
            // 建立下载
            console.log('name', result.name);
            new Download(progressBarIndex, data);
        }
    }
}
new DownloadControl();
